
.article-list .article {
  border-bottom: 1px dashed #eee;
  padding-bottom: 10px;
}
.article-list .article .info {
  color: gray;
  font-size: 12px;
}
.article-list .article h2>a {
  text-decoration: none;
  color: #0b0c0d;
}
.article-list .article h2>a:hover {
  color: red;
}



